html,
body {
  margin: 0;
  padding: 0;
  background-color: #f8f8f8;
  color: #000;
  font-family: 'Roboto-Regular', 'Open Sans', sans-serif;
}
html html,
body html {
  color: var(--color-base);
}
html h1,
body h1,
html h2,
body h2,
html h3,
body h3,
html h4,
body h4,
html h5,
body h5,
html h6,
body h6,
html p,
body p {
  margin: 0;
  padding: 0;
}
